body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Canterell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	--moz-osx-font-smoothing: grayscale;
	background: linear-gradient(
		to left,
		rgba(7, 27, 82, 1) 0%,
		rgba(0, 128, 128, 1) 100%
	);
	display: flex;
	justify-content: center;
	text-align: center;
}

.app-title {
	margin-top: 75px;
	margin-bottom: 50px;
	font-size: 75px;
	color: #0ccac4;
	font-family: 'Cormorant Infant', serif;
	font-weight: 800;
	font-style: normal;
}
